body {
  margin: 0;
  padding: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff;
  color: rgb(44, 44, 44);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* FLEX */
.flex { display: flex; }
.horizontal, .horizontal-only { flex-direction: row; }
.vertical { flex-direction: column; }
@media screen and (max-width: 720px){
  .horizontal { flex-direction: column; }
}

.flex-1 { flex: 1 1; }
.flex-2 { flex: 2 1; }
.flex-3 { flex: 3 1; }
.flex-4 { flex: 4 1; }
.flex-5 { flex: 5 1; }
.flex-6 { flex: 6 1; }
.flex-7 { flex: 7 1; }
.flex-8 { flex: 8 1; }
.flex-9 { flex: 9 1; }
.flex-10 { flex: 10 1; }

.flex-center {
  justify-content: center;
  align-items: center;
}
.flex-end {
  justify-content: flex-end;
  align-items: center;
}
.text-center { text-align: center; }

/* COMPONENT */
.img-logo {
  width: 80%;
  max-width: 200px;
  padding: 20px;
}

.itens {
  padding: 0 40px;
}
.itens > div {
  padding: 10px;
}

.TopBar {
  height: 70px;
  border-bottom: 1px solid rgb(185, 185, 185);
}
.container-body {
  height: calc(100vh - 70px);
}
@media screen and (max-width: 720px){
  .TopBar {
    height: 140px;
  }
  .container-body {
    height: auto;
  }
  @media screen and (max-width: 720px){
    .flex-end {
      justify-content: center;
      align-items: center;
    }
  }
}

.Detalhes > .base{ 
  padding: 30px;
  background-color: #fff;
}

.bottom-price {
  background-color: rgb(245, 245, 245);
  border-top: 1px solid rgb(201, 201, 201);
}

.btn-cta {
  padding: 10px 18px;
  border-radius: 5px;
  border: 0;
  background-color: rgb(192, 46, 46);
  font-size: 1.2rem;
  color: #fff;
  cursor: pointer;
}
.btn-cta:hover {
  background-color: rgb(145, 19, 19);
}
.price > span {
  font-size: 1.2rem;
}
.price {
  font-size: 1.8rem;
}

input, select {
  padding: 5px 12px;
  font-size: 1rem;
  background-color: #fefefe;
  border: 1px solid #aaa;
  border-radius: 10px;
  margin: 5px;
}
input {
  width: 30px;
}

.padding-10 {
  padding: 10px;
}
.bottom-thin {
  border-bottom: 1px solid #ddd;
}
.bottom-bold {
  border-bottom: 3px solid rgb(46, 46, 46);
}
.painel-cabecalho > div {
  cursor: pointer;
}

.details > div {
  margin: 10px 0;
  font-size: 0.9rem;
}
.black-color {
  color: #111;
}

.flex-end-end {
  justify-content: flex-end;
  align-items: flex-end;
}

.bolinha {
  background-color: rgb(233, 233, 233);
  width: 10px;
  height: 10px;
  margin: 8px;
  border-radius: 100px;
  cursor: pointer;
}

.padding-30 {
  padding: 30px;
}

.Categorias {
  height: 50px;
  -webkit-transform: all 0.3s;
          transform: all 0.3s;
}
.Categorias > div > div {
  padding: 20px;
}
@media screen and (max-width: 720px){
  .Categorias {
    height: 160px;
  }
}

.NavBar {
  border-bottom: 1px solid #ddd;
}
.container-big {
  max-width: 1024px;
  margin: 0 auto;
  width: calc(100% - 20px);
  padding: 0 10px;  
}

.Fotos {
  min-height: 400px;
}
.pesquisa-input {
  width: 200px;
  background-color: #fff;
}
.clickable {
  cursor: pointer;
}
